import React from 'react';
import axios from 'axios';
import SearchBar from './SearchBar';
import ImageList from './imageList';
import './main.css'
const API_KEY = process.env.REACT_APP_UNSPLASH_API_KEY;

class App extends React.Component  {
    state = { images: [], textblock: false, title:'' };

    onSearchSubmit = async (term) => {
        const response = await axios.get('https://api.unsplash.com/search/photos', {
            params: { query: term},
            headers: {
                Authorization: `Client-ID ${API_KEY}`
            }
        })

        this.setState({ images: response.data.results, textblock: true, title: term })
    }

    render() {
        return (
            <div class="wrapper">
                <SearchBar userSubmit={this.onSearchSubmit}/>
                <div class="app" style={{display: this.state.textblock? 'block':'none' }}>
                    <span class="title">{this.state.title}</span>
                    <br></br>
                    {this.state.images.length} images have been found.
                    <br></br><br></br><br></br>
                </div>
                <ImageList foundImages={this.state.images}/>
            </div>
        )
    }

}

export default App;